import React from 'react'

function HistoryItem(props){
    let { first, second } = props

    return (
        <li className="history__item">
            <span className='orange'>{first} </span>
                ровняется 
            <span className='orange'> {second}</span>
        </li>
    )
}

export default function History(props){
    const [history] = props.history

    return (
        <div className='history'>
            <div className="history__back">
                <h2 className="history__title">История</h2>
                <ul className="history__list">
                    { history.length 
                        && history.map((item, id) => {
                            const [first, second] = item.split(' = ')
                            return <HistoryItem key={id} {...{first, second}} /> 
                        })  
                        || <div className="history__empty">Пусто</div> 
                    }
                </ul>
            </div>
        </div>
    )
}