import React, { useState, useEffect } from 'react'

function setClasses(arr){
    return arr.join(' ').trim()
}

function SelectorItem(props){
    const { name, click, active, cls } = props

    return (
        <li className={ setClasses(['selector__item',  cls ? cls + '__item' : '',  active ? 'active' : '']) } 
            onClick={click}>{name}
        </li>
    )
}

export default function Selector(props){

    const { current, setValue, items, input, cls } = props
        , [opened, toggle] = useState(false)
        , myRef = React.createRef() 

    return (
        <div className={ setClasses(['selector', cls, opened ? 'opened' : '']) } ref={myRef}>

            <p className={ setClasses(['selector__title', cls ? cls + '__title' : '']) } onClick={() => toggle(!opened)}>
                { input }
            </p>

            <ul className={ setClasses(['selector__list', cls ? cls + '__list' : '']) }>
                {
                    items.map(item => {
                        return <SelectorItem 
                            name={item.name} 
                            key={item.code} 
                            active={item.code === current}
                            cls={cls}
                            click={
                                () => { 
                                    setValue(item)
                                    toggle(false)
                                }
                            }
                        />
                    })
                }
            </ul>

        </div>
    )
}