import React, { useState } from 'react'
import ReactDOM from 'react-dom'
import { hot } from 'react-hot-loader/root'
import currencyItems from './currency.js'
import '../scss/style.scss'

/* Components */
import Header from './header.jsx'
import Converter from './converter.jsx'
import History from './history.jsx'

function App(props){

    const [currency, setCurrency] = useState(currencyItems)
        , [currentCurrency, setCurrentCurrency] = useState(currency[5])
        , history = [...useState([])]

    return(
        <>
            <Header { ...{currency, currentCurrency, setCurrentCurrency} } />
            <Converter { ...{history} } />
            <History { ...{history} } />
        </>
    )
}

ReactDOM.render(
    <App />,
    document.getElementById('app')
)

export default hot(App)