import React, { useState } from 'react'
import Selector from './selector.jsx'

function ActualItem(props){
    const {currentItem, tempItem} = props
        , currentUsd = 1 / currentItem.course.usd
        , currentCode = currentItem.code
        , tempUsd = 1 / tempItem.course.usd
        , tempCode = tempItem.code
        , total = (currentUsd / tempUsd).toFixed(2) + ` ${currentCode}`
    
    return (
        <li className='header__actual-item'> 
            <span className='orange'>1 {tempCode}</span> = {total}
        </li>
    )
}

export default function Header(props){

    const { currentCurrency, setCurrentCurrency } = props
        , currencies = props.currency
        , currentCode = currentCurrency.code
        , tableCodes = [
                'EUR',
                'USD',
                'RUB',
                'UAH',
                'PLN',
                'CZK',
            ].filter(item => item !== currentCode.toUpperCase())

    return (
        <header className='header'>
            <div className="header__container container">
                <div className="header__change-currency">
                    <Selector 
                        current={currentCode}
                        setValue={setCurrentCurrency}
                        items={currencies}
                        input={<>
                            Ваша валюта: <span className='selector__title_currency'>{currentCode}</span>
                            </>
                        }
                    />
                </div>

                <ul className="header__actual-currency">
                    {   
                        tableCodes.map(code => {
                            const tempItem = currencies.find(item => item.code.toLowerCase() === code.toLowerCase())
                            return <ActualItem tempItem={tempItem} currentItem={currentCurrency} key={code} />
                        })
                    }
                </ul>
            </div>
        </header>
    )
}