import React, { useState } from 'react'
import Selector from './selector.jsx'
import currencies from './currency.js'

function setInput(item){
    return item 
        ? <span className='orange'>{item.name}</span> 
        : 'Выберите валюту'
}

function useSelector(active, handler){
    return (
        <Selector 
            items={ currencies }
            cls={ 'input' }
            current={ active }
            setValue={ handler }
            input={ setInput(active) }
        />
    )
}

export default function Converter(props){

    const [history, setHistory] = props.history
        , [input, setInput] = useState('')
        , [from, setFrom] = useState(null)
        , [into, setInto] = useState(null)
        , [result, setResult] = useState(null)

    function convert(){
        const value = +input

        if(!from || !into || !input || value === 0 || isNaN(value)) return;

        const koef = from.course.usd / into.course.usd
        const total = (koef * value).toFixed(2)

        const temp = value + ' ' + from.code + ' = ' + total + ' ' + into.code
        const render = <span>{ temp }</span>

        const copyHistory = history.slice()

        if(copyHistory[0] !== temp){
            copyHistory.unshift(temp)
        }

        setInput('')
        setHistory(copyHistory)
        setResult(render)
    }

    function reverseCurrence(){
        if(!from || !into) return;

        const temp = from

        setFrom(into)
        setInto(from)
    }

    return (
        <div className='converter'>
            <div className="converter__currencies">
                { useSelector(from, setFrom) }
                <button onClick={reverseCurrence}>
                    <img className='converter__arrow' src="assets/images/right-arrow.svg" alt=""/>
                </button>
                { useSelector(into, setInto) }
            </div>
            <input  type="text" 
                    name="" 
                    id="" 
                    className="converter__input input" 
                    placeholder='Введите число для конвертации' 
                    value={input} 
                    onChange={ e => setInput(e.target.value) }
            />
            <button className="converter__doit" onClick={convert}>Конвертировать</button>
            <p className="converter__result">
                { result && <> Результат: <span className='converter__result-number'>{ result }</span></> }
            </p>
        </div>
    )
}